import { Outlet } from 'react-router-dom';
import styled from 'styled-components';
import AppBg from './AppBg';
import MobileBar from './Header/MobileBar/MobileBar';
import NavigationBar from './NavigationBar';

const Layout = () => {
  return (
    <>
      <MainContainer>
        <DesktopSpacer />
        {/* <MobileBar /> */}
        <NavigationBar />
        <Outlet />
      </MainContainer>
      <AppBg />
    </>
  );
};

export default Layout;

const MainContainer = styled.div`
  display: flex;
  width: 100%;
`;
const DesktopSpacer = styled.div`
  min-width: 108px;
  height: 100vh;
  @media screen and (max-width: 720px), (max-height: 630px) {
    // MOBILE VIEW
    display: none;
  }
`;
