import styled from 'styled-components';

const CircularNeon = () => (
  <NeonCircle>
    <div className="box"></div>
  </NeonCircle>
);

export default CircularNeon;

const NeonCircle = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  align-items: center;
  display: flex;
  justify-content: center;

  .box {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: linear-gradient(107.36deg, #000000 -16.8%, #0e57a0 100%);
    transform: rotate(-360deg);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
    animation: rotate 3.5s linear infinite;
  }

  @keyframes rotate {
    100% {
      transform: rotate(360deg);
    }
  }
`;
