import { Link, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { FC, SVGProps } from 'react';
import Path from 'routes/paths';
import ButtonIcon from './ButtonIcon';

export interface NavigationButton {
  text: string;
  path: Path | string;
  icon: FC<SVGProps<SVGSVGElement>>;
  isLink?: boolean;
  isMainButton?: boolean;
}

export interface NavigationData {
  main: NavigationButton[];
  secondary: NavigationButton[];
}

const Button = ({ text, icon, path, isLink, isMainButton = false }: NavigationButton) => {
  const location = useLocation();
  const isActive = location.pathname.includes(path);

  const onClick = () => window.open(path);
  return (
    <NavLink
      onClick={() => {
        if (isLink) onClick();
      }}
      target={isLink ? '_blank' : undefined}
      to={path}
    >
      <ButtonIcon icon={icon} isActive={isActive} isMain={isMainButton} />
      <div className="button-text">{text}</div>
    </NavLink>
  );
};

export default Button;

const NavLink = styled(Link)`
  display: flex;
  margin: 0 0 0 24px;
  text-decoration: none;

  .button-text {
    display: none;
    color: white;
    user-select: none;
    width: 124px;
    min-width: 124px;
    display: flex;
    margin-left: 14px;
    opacity: 0;
    align-items: center;
    font-weight: 400;
    font-size: 1rem;
    position: relative;
  }
`;
