import MenuButton from 'components/MenuButton';
import styled from 'styled-components';
import {
  Css3,
  Js,
  React as ReactLogo,
  Html5,
  Typescript,
  Firebase,
  Git,
  Mobx,
  Mui,
  Redux,
  Npm,
} from 'assets/svgs';
import Slider from 'components/Slider';

const About = () => {
  const content1 = [
    { text: 'CSS 3', logo: <Css3 /> },
    { text: 'React Native', logo: <ReactLogo /> },
    { text: 'React.js', logo: <ReactLogo /> },
    { text: 'HTML5', logo: <Html5 /> },
    { text: 'Javascript', logo: <Js /> },
    { text: 'Typescript', logo: <Typescript /> },
  ];
  const content2 = [
    { text: 'Firebase', logo: <Firebase /> },
    { text: 'Git', logo: <Git /> },
    { text: 'Mobx', logo: <Mobx /> },
    { text: 'Mui', logo: <Mui /> },
    { text: 'Redux', logo: <Redux /> },
    { text: 'Npm', logo: <Npm /> },
  ];
  return (
    <MainContainer>
      <AboutInfo>
        <h1>Omer Zabtani</h1>
        <p>
          Web Developer. Focused On Frontend development with React. here is some of my Techs and tools i use.
        </p>
      </AboutInfo>

      <Container>
        <Slider
          showArrows
          imgs={[<MenuButton content={content1} />, <MenuButton content={content2} />]}
          resoultionsSlideCount={{ desktop: 2, mobile: 1 }}
        />
      </Container>
    </MainContainer>
  );
};

export default About;
const AboutInfo = styled.div`
  margin-top: 150px;
  h1 {
    font-size: 3rem;
    margin: 0;
  }
  p {
    max-width: 600px;
    padding: 0;
    margin: 0;
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  padding: 20px;
  @media screen and (max-width: 600px) {
    h1 {
      padding-top: 100px;
      padding-left: 25px;
      font-size: 1.5rem;
      margin: 0;
    }
    p {
      padding-left: 25px;
      margin: 0;
    }
  }
`;
const MainContainer = styled.div`
  overflow: hidden;
  min-height: 660px;
  .menu:first-child {
    margin-top: 0;
  }
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  align-items: center;
`;

const Container = styled.div`
  .mySwiper {
    position: relative;
    bottom: 80px;
    div {
      background-color: transparent;
    }
    height: 660px;
    width: 780px;
    scale: 0.7;

    @media screen and (max-width: 850px) {
      scale: 0.65;
    }
    @media screen and (max-width: 650px) {
      scale: 0.6;
    }
    @media screen and (max-width: 600px) {
      scale: 0.55;
    }
    @media screen and (max-width: 550px) {
      scale: 0.5;
    }
    @media screen and (max-width: 500px) {
      scale: 0.45;
    }
    @media screen and (max-width: 450px) {
      scale: 0.4;
    }
    @media screen and (max-width: 400px) {
      scale: 0.35;
    }
    @media screen and (max-width: 350px) {
      scale: 0.3;
    }
    @media screen and (max-width: 300px) {
      scale: 0.25;
    }
  }
  justify-content: space-between;
  flex-direction: column;
  display: flex;
`;
