import { Zoom, Fade, Button, Collapse, List, ListItem, ListItemIcon } from '@mui/material';
import { useRef, useState } from 'react';
import styled from 'styled-components';
import useOnScrren from '../hooks/useOnScreen';
import Slider from '../Slider';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import CodeIcon from '@mui/icons-material/Code';
import { TECHS_LOGOS } from 'components/techs-logos';
import GitHubIcon from '@mui/icons-material/GitHub';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';

type Tech = keyof typeof TECHS_LOGOS;
interface Props {
  idx: number;
  name: string;
  imgs: string[];
  description: string;
  techs: Tech[];
  features: string[];
}

const Project = ({ idx, name, imgs, description, techs, features }: Props) => {
  const sectionRef = useRef<HTMLDivElement>(null);
  const isVisible = useOnScrren(sectionRef);
  const [expanded, setExpanded] = useState(false);
  const featuresIcon = expanded ? <ExpandLessIcon /> : <ExpandMoreIcon />;
  const content = (
    <Section>
      <div className="content-container">
        <SectionHeader>
          <h1> {name} </h1>
          <Techs>{techs.map((tech) => TECHS_LOGOS[tech])}</Techs>
        </SectionHeader>
        <p>{description}</p>
        <div
          style={{
            margin: '30px auto 30px auto',
            width: '90%',
            display: 'flex',
            justifyContent: 'space-between',
          }}
        >
          <Button sx={{ width: 'fit-content' }} variant="outlined" startIcon={<PlayCircleIcon />}>
            Live Demo
          </Button>
          <Button sx={{ width: 'fit-content' }} variant="outlined" startIcon={<GitHubIcon />}>
            View Code
          </Button>
        </div>
        <Button
          sx={{ width: 'fit-content', margin: 'auto auto 15px auto' }}
          onClick={() => setExpanded((prev) => !prev)}
          endIcon={featuresIcon}
        >
          App features
        </Button>
        <Collapse in={expanded} timeout={80} unmountOnExit>
          <List>
            {features.map((feature: string, idx: number) => (
              <ListItem sx={{ color: 'white' }} key={idx}>
                <ListItemIcon sx={{ color: 'white' }}>
                  <CodeIcon />
                </ListItemIcon>
                {feature}
              </ListItem>
            ))}
          </List>
        </Collapse>
      </div>
      <div className="g">
        <Slider imgs={imgs} resoultionsSlideCount={{ desktop: 2, mobile: 1 }} />
      </div>
    </Section>
  );
  return (
    <div style={{ overflow: 'hidden', paddingTop: '40px' }}>
      <SectionWrapper ref={sectionRef}>{content}</SectionWrapper>
    </div>
    //   <div style={{ overflow: 'hidden', paddingTop: '40px' }}>
    //   <SectionWrapper ref={sectionRef}>
    //     <Fade timeout={1400} in={idx === 0 ? true : isVisible}>
    //       {content}
    //     </Fade>
    //   </SectionWrapper>
    // </div>
  );
};

export default Project;

const SectionHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  h1 {
    line-height: 100%;
    justify-self: flex-start;
    margin: 8px 0 0 20px;

    color: rgb(25, 118, 210);
    //  color: rgb(0, 46, 84);
    @media only screen and (max-width: 850px) {
      font-size: 1.3rem;
    }
  }
`;
const Techs = styled.div`
  display: flex;
  padding: 10px;
  background-image: linear-gradient(62deg, #00499266 0%, #000000ae 150%);
  border-radius: 10px 10px 0 10px;
  svg {
    margin-right: 10px;
    height: 30px;
    width: 30px;

    @media only screen and (max-width: 850px) {
      margin-right: 5px;
      width: 20px;
      height: 20px;
    }
  }
`;

const SectionWrapper = styled.div`
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: center;
`;

const Section = styled.div`
  width: 65vw;
  max-width: 800px;
  border: 1px solid rgb(25, 118, 210);
  // border: 1px solid rgb(25, 118, 210);

  border-radius: 10px;
  @media only screen and (max-width: 650px) {
    width: 95vw;
    .MuiButton-root {
      font-size: 60%;
    }
  }
  z-index: 11;
  display: flex;
  flex-direction: column;
  position: relative;
  &:after {
    content: '';

    border-radius: 10px;

    background-color: transparent;
    // background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
    opacity: 0.5;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
  }
  .content-container {
    li:first-child {
      padding-top: 0 !important;
      margin-top: 0;
    }
    ul {
      padding-top: 0;
      padding-left: 5px;
    }
    p {
      font-weight: 100;
      color: whitesmoke;
      margin: 0;
      padding: 20px;
    }
    width: 100%;

    display: flex;
    flex-direction: column;
  }
`;
